/*
 * Breakpoints:
 * 1200, 960, 768, 560, 480
 */
/******************************
 * Elements
 *****************************/
/* Scrollbar */
/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #008278 #dfe9eb;
}

/* Chrome, Edge and Safari */
*::-webkit-scrollbar {
  width: 4px;
  width: 4px;
}

*::-webkit-scrollbar-track {
  border-radius: 4px;
  background-color: #dfe9eb;
}

*::-webkit-scrollbar-track:hover {
  background-color: #d4dddf;
}

*::-webkit-scrollbar-track:active {
  background-color: #c9d2d4;
}

*::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: #008278;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #009e92;
}

*::-webkit-scrollbar-thumb:active {
  background-color: #39ac2d;
}

/* Base */
/* Colors */
* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  font-size: 16px;
  background: #f5f5f5;
}

body {
  overflow-x: hidden;
  overflow-y: auto;
}
body.noscroll {
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #3d3d3d;
  text-decoration: none;
}

a {
  transition: all 0.3s;
}
a:hover {
  color: #1755ce;
}

main.site-main {
  margin: 2rem 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}
@media screen and (max-width: 1200px) {
  .container {
    padding: 0 20px !important;
    max-width: 960px !important;
    width: 100% !important;
  }
}
@media screen and (max-width: 960px) {
  .container {
    max-width: 768px !important;
  }
}
@media screen and (max-width: 768px) {
  .container {
    max-width: unset;
    width: 100%;
  }
}
@media screen and (max-width: 375px) {
  .container {
    padding: 0 10px !important;
  }
}

/* Buttons */
a[type=button],
button {
  cursor: pointer;
}

button.label,
a[type=button] {
  background: none;
  border: none;
}

a[type=button].modal-btn {
  padding: 0 40px;
  height: 50px;
  background: linear-gradient(90.6deg, #0f9502 19.86%, #39ac2d 81.94%);
  border-radius: 15px;
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 10px;
  width: max-content;
  transition: all 0.1s ease-in;
}
a[type=button].modal-btn img {
  width: 20px;
  filter: invert(1);
}
a[type=button].modal-btn span {
  font-family: "Museo Sans Cyrl 700";
  color: #fff;
}
a[type=button].modal-btn:hover {
  transform: scale(1.05);
  box-shadow: 4px 4px 0px #b8ceff;
}

a[type=button].action-btn {
  padding: 0 20px;
  height: 50px;
  background: linear-gradient(90.6deg, #0f9502 19.86%, #39ac2d 81.94%);
  border-radius: 15px;
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 10px;
  width: max-content;
  transition: all 0.1s ease-in;
}
a[type=button].action-btn img {
  width: 20px;
  filter: invert(1);
}
a[type=button].action-btn i {
  font-size: 20px;
  color: #fff;
}
a[type=button].action-btn span {
  font-family: "Museo Sans Cyrl 700";
  color: #fff;
}
a[type=button].action-btn:hover {
  transform: scale(1.05);
  box-shadow: 4px 4px 0px #b8ceff;
}

