body {
  font-family: "Museo Sans Cyrl 300";
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Museo Sans Cyrl 700";
}

h2.title {
  font-size: 2.2rem;
  margin: 0 auto;
  text-align: center;
  color: #3d3d3d;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
@media screen and (max-width: 560px) {
  h2.title {
    font-size: 1.6rem;
    max-width: 200px;
  }
}

/* Header */
header.site-header {
  width: 100%;
}
@media screen and (max-width: 1200px) {
  header.site-header {
    display: none;
  }
}
header.site-header div .container,
header.site-header nav .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 6px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (max-width: 1200px) {
  header.site-header div .container,
header.site-header nav .container {
    gap: 1rem;
  }
}
@media screen and (max-width: 580px) {
  header.site-header div .container,
header.site-header nav .container {
    gap: 10px;
  }
}
header.site-header div .container p,
header.site-header nav .container p {
  margin: 0;
}
header.site-header .header-top {
  background: linear-gradient(90deg, rgba(0, 130, 120, 0.135) 0%, rgba(0, 130, 120, 0) 100%);
}
header.site-header .header-top p,
header.site-header .header-top a {
  font-family: "Museo Sans Cyrl 700";
}
header.site-header .header-top span.green {
  color: #39ac2d;
}
header.site-header .header-controls {
  background: #ffffff;
}
header.site-header .header-controls .container {
  height: 100%;
}
header.site-header .header-controls .container .vertical-information {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100%;
}
header.site-header .header-controls .container .vertical-information p.title {
  font-size: 1.4rem;
  color: #008278;
}
@media screen and (max-width: 1200px) {
  header.site-header .header-controls .container .vertical-information p.title {
    font-size: 1rem;
  }
}
header.site-header .header-controls .container .vertical-information .phone-wrapper {
  display: inline-flex;
  align-items: flex-end;
  flex-direction: row;
  gap: 1rem;
}
@media screen and (max-width: 580px) {
  header.site-header .header-controls .container .vertical-information .phone-wrapper {
    gap: 10px;
  }
}
header.site-header .header-controls .container .vertical-information .phone-wrapper span {
  color: #1755ce;
  cursor: pointer;
}
header.site-header .header-controls .container .vertical-information a.phone {
  font-size: 1.3rem;
  font-weight: 700;
  transition: 0.5s;
}
header.site-header .header-controls .container .vertical-information a.phone:hover {
  color: #39ac2d;
}
@media screen and (max-width: 1200px) {
  header.site-header .header-controls .container .vertical-information a.phone {
    font-size: 1rem;
  }
}
header.site-header .header-controls .container .search label {
  position: relative;
  /* @media screen and (max-width: 1200px) {
    span {
      display: none;
    }
  } */
}
header.site-header .header-controls .container .search label input {
  height: 40px;
  background: #ffffff;
  border: 0.2px solid #898989;
  border-radius: 10px;
  width: 200px;
  padding: 0 10px;
  transition: 0.3s;
  /* @media screen and (max-width: 1200px) {
    width: 40px;
    padding: 0;
  } */
}
@media screen and (max-width: 580px) {
  header.site-header .header-controls .container .search label input {
    max-width: 200px;
    min-width: 100px;
    width: 100%;
  }
}
header.site-header .header-controls .container .search label input:focus-visible {
  border: 1px solid #008278;
  outline: 0;
  box-shadow: 0 0 10px rgba(0, 130, 119, 0.4666666667);
}
header.site-header .header-controls .container .search label i {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  /* @media screen and (max-width: 1200px) {
    font-size: 1.2rem;
    opacity: 0.7;
    left: 50%;
    transform: translate(-50%, -50%);
  } */
  pointer-events: none;
}
header.site-header .header-controls .container .search label span {
  position: absolute;
  top: 50%;
  left: 34px;
  transform: translateY(-50%);
  pointer-events: none;
}
@keyframes focus-animation {
  0% {
    transform: translateX(0) translateY(-50%);
    opacity: 1;
  }
  100% {
    transform: translateX(-10px) translateY(-50%);
    opacity: 0;
  }
}
header.site-header .header-controls .container .search label.focused i,
header.site-header .header-controls .container .search label.focused span {
  animation: focus-animation 0.3s forwards linear;
  /* @media screen and (max-width: 1200px) {
    display: none;
  } */
}
header.site-header .header-controls .container .search label.focused input {
  padding: 0 10px;
  /* @media screen and (max-width: 1200px) {
    width: 200px;
  } */
}
header.site-header .header-controls .container .client-buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media screen and (max-width: 580px) {
  header.site-header .header-controls .container .client-buttons {
    gap: 10px;
  }
}
header.site-header .header-controls .container .client-buttons .auth button {
  background: none;
  border: none;
}
header.site-header nav.header-links {
  background: #008278;
}
header.site-header nav.header-links ul.header-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  width: 100%;
}
@media screen and (max-width: 580px) {
  header.site-header nav.header-links ul.header-menu {
    gap: 10px;
  }
}
header.site-header nav.header-links ul.header-menu li.menu-item {
  position: relative;
}
header.site-header nav.header-links ul.header-menu li.menu-item a {
  text-transform: uppercase;
  color: #fff;
  font-family: "Museo Sans Cyrl 500";
  transition: all 0.3s;
}
header.site-header nav.header-links ul.header-menu li.menu-item a:hover {
  color: #f6fda4;
}
header.site-header nav.header-links ul.header-menu li.menu-item ul.sub-menu {
  display: none;
  background: #ffffff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  border-radius: 0 0 20px 20px;
  position: absolute;
  top: calc(100% + 6px);
  width: max-content;
  padding: 20px;
}
header.site-header nav.header-links ul.header-menu li.menu-item ul.sub-menu li.sub-menu-item a {
  color: #222222;
  text-transform: none;
}
header.site-header nav.header-links ul.header-menu li.menu-item ul.sub-menu li.sub-menu-item a:hover {
  color: #1755ce;
}

header.site-header.mobile {
  display: flex;
  padding: 10px 0;
  background: #008278;
  /* #mobile-menu {
    display: none;
  } */
  /* &.menu-toggled { */
}
@media screen and (min-width: 1200px) {
  header.site-header.mobile {
    display: none;
  }
}
header.site-header.mobile .header-controls {
  background: none;
  width: 100%;
}
header.site-header.mobile .header-controls .logo {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 580px) {
  header.site-header.mobile .header-controls .logo {
    width: 100px;
  }
  header.site-header.mobile .header-controls .logo img {
    width: 100%;
  }
}
header.site-header.mobile .header-controls .menu-buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media screen and (max-width: 580px) {
  header.site-header.mobile .header-controls .menu-buttons {
    gap: 10px;
  }
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media screen and (max-width: 580px) {
  header.site-header.mobile .header-controls .menu-buttons .client-buttons {
    gap: 10px;
  }
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons div {
  width: 20px;
  height: 20px;
  cursor: pointer;
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons div img {
  height: 100%;
  width: 100%;
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons .menu-toggle {
  height: 30px;
  width: 30px;
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons .menu-toggle button {
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 0;
  position: relative;
  height: inherit;
  width: inherit;
  padding: 0;
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons .menu-toggle button img {
  height: 100%;
  width: 100%;
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons .menu-toggle button span {
  color: #39ac2d;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 0.8rem;
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons .menu-toggle button span.opened {
  display: none;
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons .menu-toggle button.toggled span.opened {
  display: block;
}
header.site-header.mobile .header-controls .menu-buttons .client-buttons .menu-toggle button.toggled span.closed {
  display: none;
}
header.site-header.mobile #mobile-menu {
  overflow-x: hidden;
  overflow-y: auto;
  position: fixed;
  z-index: 100;
  top: 60px;
  left: 0;
  height: calc(100% - 60px);
  width: 100%;
  background: linear-gradient(168.36deg, rgba(0, 130, 120, 0.9), #39ac2d);
  padding: 20px;
  display: grid;
  grid-template: "links phone" "sublinks phone" auto/1fr 1fr;
}
@media screen and (max-width: 580px) {
  header.site-header.mobile #mobile-menu {
    grid-template: "links" "phone" "sublinks" auto/1fr;
    gap: 1rem;
  }
}
header.site-header.mobile #mobile-menu ul.header-menu {
  grid-area: links;
}
header.site-header.mobile #mobile-menu ul.header-menu li {
  margin-bottom: 10px;
}
header.site-header.mobile #mobile-menu ul.header-menu li:last-child {
  margin-bottom: 0;
}
header.site-header.mobile #mobile-menu ul.header-menu li a {
  color: #fff;
  font-size: 1.4rem;
  position: relative;
}
@media screen and (max-width: 580px) {
  header.site-header.mobile #mobile-menu ul.header-menu li a {
    font-size: 1rem;
  }
}
header.site-header.mobile #mobile-menu ul.header-menu li a::before {
  display: block;
  content: "";
  position: absolute;
  top: -2px;
  left: -4px;
  height: calc(100% + 4px);
  width: 0;
  background: linear-gradient(to right, #1755ce, #3169d9);
  transition: all 0.3s;
  z-index: -1;
}
header.site-header.mobile #mobile-menu ul.header-menu li a:hover::before {
  width: calc(100% + 8px);
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child a {
  position: relative;
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child a::after {
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  content: "\f107";
  margin-left: 10px;
  font-size: 1rem;
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child a.active::before {
  display: block;
  content: "";
  position: absolute;
  top: -2px;
  left: -4px;
  height: calc(100% + 4px);
  background: linear-gradient(to right, #1755ce, #3169d9);
  transition: all 0.3s;
  /* z-index: -1; */
  width: calc(100% + 8px) !important;
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child a.active::after {
  content: "\f106";
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child ul.sub-menu {
  display: none;
  position: relative;
  margin: 1rem 0;
  padding: 20px;
  border-radius: 15px;
  background: #51b279;
  width: max-content;
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child ul.sub-menu li {
  margin: 0;
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child ul.sub-menu li a {
  font-size: 1rem;
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child ul.sub-menu li a:hover {
  text-decoration: underline;
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child ul.sub-menu li a::before {
  display: none;
}
header.site-header.mobile #mobile-menu ul.header-menu li.has-child ul.sub-menu li a::after {
  content: "";
}
header.site-header.mobile #mobile-menu .sublinks {
  grid-area: sublinks;
  display: flex;
  flex-direction: column;
  align-self: end;
  margin-top: 2rem;
}
@media screen and (max-width: 580px) {
  header.site-header.mobile #mobile-menu .sublinks {
    align-self: unset;
    margin-top: 0;
  }
}
header.site-header.mobile #mobile-menu .sublinks a {
  color: #f5f5f5;
  font-size: 1rem;
  margin-bottom: 10px;
}
@media screen and (max-width: 580px) {
  header.site-header.mobile #mobile-menu .sublinks a {
    font-size: 0.8rem;
  }
}
header.site-header.mobile #mobile-menu .sublinks a:last-child {
  margin: 0;
}
header.site-header.mobile #mobile-menu .sublinks a:hover {
  text-decoration: underline;
}
header.site-header.mobile #mobile-menu .contact {
  grid-area: phone;
  padding: 20px 40px;
  background: #fff;
  border-radius: 15px;
  width: max-content;
  height: max-content;
  justify-self: end;
}
@media screen and (max-width: 580px) {
  header.site-header.mobile #mobile-menu .contact {
    justify-self: start;
  }
}
header.site-header.mobile #mobile-menu .contact .title {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
header.site-header.mobile #mobile-menu .contact .title span {
  font-family: "Museo Sans Cyrl 500";
  margin-left: 4px;
}
header.site-header.mobile #mobile-menu .contact .phone {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
header.site-header.mobile #mobile-menu .contact .phone .title {
  color: #39ac2d;
  font-size: 0.8rem;
  margin: 0;
}
header.site-header.mobile #mobile-menu .contact .phone:first-child a.phone {
  font-size: 1.2rem;
}
header.site-header.mobile #mobile-menu .contact .phone a.phone {
  font-family: "Museo Sans Cyrl 500";
  margin: 0;
}
header.site-header.mobile #mobile-menu .contact .open {
  margin-bottom: 25px;
}
header.site-header.mobile #mobile-menu .contact .open p {
  margin: 0;
  font-family: "Museo Sans Cyrl 500";
}
header.site-header.mobile #mobile-menu .contact .open p.title {
  font-size: 0.8rem;
  font-family: "Museo Sans Cyrl 300";
}
header.site-header.mobile #mobile-menu .contact a[type=button] {
  color: #fff;
}

