/* Footer */
footer.site-footer {
  background: linear-gradient(90.68deg, #083288 2.49%, rgba(0, 130, 120, 0.72) 99.61%, rgba(0, 130, 120, 0.72) 99.61%);
  padding: 40px 0;
}
footer.site-footer .container {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
footer.site-footer .container div a,
footer.site-footer .container div p {
  color: #fff;
}
footer.site-footer .container .footer-links {
  display: grid;
  grid-template: auto/1fr 1fr 1fr;
}
@media screen and (max-width: 768px) {
  footer.site-footer .container .footer-links {
    gap: 2rem;
  }
}
@media screen and (max-width: 580px) {
  footer.site-footer .container .footer-links {
    gap: 4px;
    grid-template: auto/1fr;
  }
}
footer.site-footer .container .footer-links .column {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media screen and (max-width: 580px) {
  footer.site-footer .container .footer-links .column {
    gap: 4px;
  }
}
footer.site-footer .container .footer-links .column a {
  text-transform: uppercase;
  font-family: "Museo Sans Cyrl 700";
  font-size: 1.2rem;
}
footer.site-footer .container .footer-links .column a:hover {
