/* Sweet Alert Popups */
.swal2-popup {
  max-width: 420px !important;
  width: 100% !important;
  overflow-x: hidden;
  overflow-y: auto;
}
.swal2-popup .swal2-close {
  position: absolute;
  right: 0;
  top: 0;
}
.swal2-popup .swal2-html-container {
  margin: 3rem 3rem 0.8rem 3rem !important;
  overflow: visible;
  font-size: 1rem;
}
.swal2-popup .swal2-html-container .phone-container span,
.swal2-popup .swal2-html-container .phone-container p,
.swal2-popup .swal2-html-container .phone-container a {
  font-family: "Museo Sans Cyrl 500";
}
.swal2-popup .swal2-html-container .phone-container .title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}
.swal2-popup .swal2-html-container .phone-container .title img {
  margin-right: 10px;
}
.swal2-popup .swal2-html-container .phone-container div.phone {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
  position: relative;
}
.swal2-popup .swal2-html-container .phone-container div.phone::after {
  position: absolute;
  content: "";
  top: calc(100% + 1rem);
  left: 50%;
  transform: translateX(-50%);
  background: #969595;
  width: 25px;
  height: 1px;
}
.swal2-popup .swal2-html-container .phone-container div.phone:nth-child(4n)::after {
  display: none;
}
.swal2-popup .swal2-html-container .phone-container div.phone .title {
  font-size: 1rem;
  color: #39ac2d;
  margin: 0;
  margin-bottom: 4px;
}
