  font-size: 1.2rem;
  transition: all 0.3s;
}
.swal2-popup .swal2-html-container .phone-container div.phone a.phone:hover {
  color: #1755ce;
}
.swal2-popup .swal2-html-container .phone-container div.open p {
  margin: 0;
  font-family: "Museo Sans Cyrl 300";
}
.swal2-popup .swal2-html-container .phone-container div.open p.title {
  font-size: 0.9rem;
  margin-bottom: 4px;
}
